.main{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;  
  .content{
    width: 800px;
    height: 500px;
    background-color: #2e2f30;
    border-radius: 8px; 
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    h1 {
      font-weight: 400;
      font-size: 35px;
    }
  }
}

.btn{
  background-color: #ff5f5f;
  color: #fff;
  border: none;
  border-radius: 8px;
  width: 140px;
  height: 60px;
  font-size: 20px;
  font-weight: 600;
  
}
